import React, { useState,useEffect } from 'react';
import './index.css';
import { Link, useNavigate } from "react-router-dom";
import { signup } from '../../../apiIntegrations/auth';

function SignupForm() {

  const navigate = useNavigate();

  const [isSignIN, setIsSignIN] = useState(null);
  useEffect(() => {
    const token = localStorage.getItem("Authorization");
    console.log(token);
    if (token) {
      setIsSignIN(true);
      navigate('/login');
    }
    else setIsSignIN(false);
  }, []);


  const [first_name, setFirstName] = useState('');
  const [last_name, setLastName] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');

  const handleSubmit = (event) => {
    event.preventDefault();


    // Reset the form
    setFirstName('');
    setLastName('');
    setEmail('');
    setPassword('');
  };

  return (
    <div className="sign-up-form-container">

      <form onSubmit={handleSubmit}>

        <div className="firstName">First Name</div>
        <div className='input-signup-search'>
          <input
            type="text"
            id="firstName"
            value={first_name}
            onChange={(event) => setFirstName(event.target.value)}
            style={{ border:'none', marginLeft:0 }}
          />
          </div>

          <div>
            <div className="lastName">Last Name</div>
            <div className='input-signup-search'>
            <input
              type="text"
              id="lastName"
              value={last_name}
              onChange={(event) => setLastName(event.target.value)}
              style={{ border:'none', marginLeft:0 }}
            />
            </div>
          </div>
          <div>
            <div className="email">E-mail ID</div>
            <div className='input-signup-search'>
            <input
              type="email"
              id="email"
              value={email}
              onChange={(event) => setEmail(event.target.value)}
              style={{ border:'none', marginLeft:0 }}
            />
            </div>
          </div>
          <div>
            <div className="password">Password</div>
            <div className='input-signup-search'>
            <input
              type="password"
              id="password"
              value={password}
              onChange={(event) => setPassword(event.target.value)}
              style={{ border:'none', marginLeft:0 }}
            />
            </div>
          </div>
          <button
          onClick={async () => {
            let res = await signup({ first_name, last_name, email, password });
            console.log(res.token);
            if (res.error) {
              alert(res?.error?.response?.data?.message);
            } else {
              localStorage.setItem("Authorization", res.token);
              setIsSignIN(true);
              navigate('/login');
            }
          }}
           type="submit" className='sign-up-button'>Sign Up</button>
      </form>
    </div>
  );
}

export default SignupForm;
