import React from "react";
import './index.css';
import { Link, useLocation } from 'react-router-dom';

function TabButton() {
    const location = useLocation()
    const homeStyle = {
        backgroundColor: location.pathname === '/signup' ? '#E15456' : 'white',
        color: location.pathname === '/signup' ? '#fffff' : '#464A6B', 
        // set the background color for the home page
    };
    const loginStyle = {
        backgroundColor: location.pathname === '/login' ? '#E15456' : 'white',
        color: location.pathname === '/login' ? '#fffff' : '#464A6B', 
        // set the background color for the about page
    };
    return (
        <div className="signbutton-container">
            <Link to="/signup" style={homeStyle}>
                Sign up
            </Link>

            <Link to="/login" style={loginStyle}>
                Log in
            </Link>
        </div>

    );
}
export default TabButton;