import React, { useState, useEffect } from 'react';
import './index.css';
import { Link, useNavigate } from "react-router-dom";
import { login } from '../../../apiIntegrations/auth';

function LoginForm() {

  const navigate = useNavigate();

  const [isLoggedIN, setIsLoggedIN] = useState(null);
  useEffect(() => {
    const token = localStorage.getItem("Authorization");
    console.log(token);
    if (token) {
      setIsLoggedIN(true);
      navigate('/home');
    }
    else setIsLoggedIN(false);
  }, []);

  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');

  const handleSubmit = async (event) => {
    event.preventDefault();


    // try {
    //   const response = await fetch('http://localhost:3003/login', {
    //     method: 'POST',
    //     headers: {
    //       'Content-Type': 'application/json'
    //     },
    //     body: JSON.stringify({ email, password })
    //   });

    //   if (response.ok) {
    //     const data = await response.json();
    //     console.log(data); // do something with the response data
    //   } else {
    //     console.log('HTTP error: ' + response.status);
    //   }
    // } catch (error) {
    //   console.error('Error:', error);
    // }

    setEmail('');
    setPassword('');

  };


  return (
    <div className="login-form-container">

      <form onSubmit={handleSubmit}>
        <div>
          <div className="login-form-email">E-mail ID</div>
          <div className='input-login-search'>
            <input
              type="email"
              id="email"
              value={email}
              onChange={(event) => setEmail(event.target.value)}
              style={{ border: 'none', marginLeft: 0 }}
            />
          </div>
        </div>
        <div>
          <div className="login-form-password">Password</div>
          <div className='input-login-search'>
            <input
              type="password"
              id="password"
              value={password}
              onChange={(event) => setPassword(event.target.value)}
              style={{ border: 'none', marginLeft: 0 }}
            />
          </div>
        </div>
        <div className='button'>
          {/* <Link to='/home' > */}
          <button
            onClick={async () => {
              let res = await login({ email, password });
              console.log(res.token);
              if (res.error) {
                alert(res?.error?.response?.data?.message);
              } else {
                localStorage.setItem("Authorization", res.token);
                setIsLoggedIN(true);
                navigate('/home');
              }
            }}
            type="submit" className='log-in-button'>Login</button>
          {/* </Link> */}
        </div>
      </form>
    </div>
  );
}

export default LoginForm;
